@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Ink+Free:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
