  /* font sizes */
  --font-size-xl: 20px;
  --font-size-13xl: 32px;
  --font-size-3xs: 10px;
  --font-size-xs: 12px;
  --font-size-sm: 14px;
  --font-size-mini: 15px;
  --font-size-smi: 13px;
  --font-size-base: 16px;
  --font-size-5xl: 24px;
  --font-size-6xs: 7px;
  --font-size-2xs: 11px;

  /* Colors */
  --color-black: #000;
  --color-mistyrose: #f8dfdb;
  --color-palegreen-100: #85ffa8;
  --color-palegreen-200: #73ff9c;
  --color-palegreen-300: #77fa9d;
  --color-palegreen-400: #79f89d;
  --color-palegreen: #77f89b;
  --color-whitesmoke-200: #f6f6f6;
  --color-whitesmoke-100: #f5f5f5;
  --color-whitesmoke-300: #f2f2f2;
  --color-whitesmoke: #f3f1f1;
  --color-white: #fff;
  --color-gainsboro-100: #e8e8e8;
  --color-gainsboro-200: #e7e7e7;
  --color-darkgray-200: #afafaf;
  --color-darkgray-300: #adadad;
  --color-darkgray-400: #ababab;
  --color-darkgray-500: #a8a8a8;
  --color-darkgray-100: #a7a7a7;
  --color-darkgray-600: #a5a5a5;
  --color-darkgray-700: #a1a1a1;
  --color-darkgray-800: #a0a0a0;
  --color-lightgray-100: #d5d5d5;
  --color-lightgray-200: #d1d1d1;
  --color-silver-200: #c7c7c7;
  --color-silver-300: #c6c6c6;
  --color-silver-100: #bfbfbf;
  --color-silver-400: #b7b7b7;
