  /* Border radiuses */
  --br-81xl: 100px;
  --br-11xs: 2px;
  --br-4xl: 23px;
  --br-29xl: 48px;
  --br-7xs: 6px;
  --br-8xs: 5px;
  --br-3xs: 10px;
  --br-2xl: 21px;
  --br-sm: 14px;
  --br-2xs: 11px;
  --br-6xl: 25px;
  --br-base: 16px;
  --br-9xs: 4px;
  --br-10xs: 3px;
  --br-12xl: 31px;
  --br-xl: 20px;
